import React, {
  useCallback, useMemo
} from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, HStack, Button
} from '@chakra-ui/react';
import { format } from 'date-fns';
import { shallowEqual } from 'react-redux';
import style from './TransactionDetails.module.css';
import { transaction } from '../../../types/transaction';
import { useAppSelector } from '../../../hooks/store';
import { currencyFormat, getInTouch } from '../../../utils/general';
import SkeletonLoader from '../../../components/SkeletonLoader';
import Map from '../../../components/Map';

interface Props {
  data: transaction,
  showReportButton?: boolean,
  showPdfButton?: boolean,
  amountFontWeight?: string,
  amountFontSize?: string,
  amountReceivedColor?: string,
  amountSentColor?: string
  dateFormat?: string
  onExport?: () => void
  // attachmentsFullWidth?: boolean,
}

const TransactionDetails = ({
  data, showReportButton, showPdfButton, amountFontWeight, amountFontSize, amountSentColor, amountReceivedColor, dateFormat = "MMM d, yyyy 'at' h:mm aa", onExport
}: Props) => {
  const { t } = useTranslation();

  const accounts = useAppSelector((store) => store.account.accounts, shallowEqual);
  const selectedAccount = useAppSelector((store) => store.crypto.selectedAccount, shallowEqual);

  const otherDetails = useMemo(() => {
    let source; let paidTo; let receivedFrom; let description; let transactionId; let
      location; let destination; let bank;
    if (data.walletId && data.transferType) {
      const { transferType } = (data as any);
      const typeData = (data as any)[transferType];
      const account = accounts.find((a) => a.id === typeData.accountId);
      description = data.description;
      switch (transferType) {
        case 'buy':
          source = account ? account.label : typeData.accountId;
          destination = selectedAccount?.label;
          break;
        case 'sell':
          source = selectedAccount?.label;
          destination = account ? account.label : typeData.accountId;
          break;
        case 'send':
          source = selectedAccount?.label;
          destination = typeData.destinationCryptoAddress;
          break;
        case 'receive':
          destination = selectedAccount?.label;
          source = 'zerohash';
          break;
        default:
      }
    } else {
      // @TODO: Needs to handle credit txnType also.
      transactionId = data.id;
      description = data.description;
      switch (
        data.transferType
      ) {
        case 'intrabank':
          if (
            data.txnType === 'debit'
          ) {
            source = data.accountId;
            destination = data.intrabank?.name;
          } else {
            source = data.intrabank?.name;
            destination = data.accountId;
          }
          bank = data.intrabank?.bankName;
          break;
        case 'ach':
          if (
            data.txnType === 'debit'
          ) {
            source = data.accountId;
            destination = data.ach?.name;
          } else {
            source = data.ach?.name;
            destination = data.accountId;
          }

          bank = data.ach?.bankName;
          break;
        case 'domesticWire':
          if (
            data.txnType === 'debit'
          ) {
            source = data.accountId;
            destination = data.domesticWire?.name || data.domesticWire?.accountNumber;
          } else {
            source = data.domesticWire?.name;
            destination = data.accountId;
          }
          bank = data.domesticWire?.bankName;
          break;
        case 'internationalWire':
          source = data.accountId;
          if (
            data.txnType === 'debit'
          ) {
            source = data.accountId;
            destination = data.internationalWire?.name || data.internationalWire?.accountNumber;
          } else {
            source = data.internationalWire?.name;
            destination = data.accountId;
          }
          bank = data.internationalWire?.beneficiaryBank;
          break;
        case 'physicalCheck':
          if (
            data.txnType === 'debit'
          ) {
            source = data.accountId;
            destination = data.physicalCheck?.name || data.domesticWire?.accountNumber;
          } else {
            source = data.physicalCheck?.name;
            destination = data.accountId;
          }
          break;
        case 'card':
          if (
            data.txnType === 'debit'
          ) {
            source = data.card?.label;
            destination = data.card?.merchant?.name || data.card?.merchant?.merchantName;
          } else {
            source = data.card?.label;
            destination = data.card?.merchant?.name || data.card?.merchant?.merchantName;
          }
          break;
        case 'solidCard':
          source = data.solidCard?.name;
          if (
            data.txnType === 'debit'
          ) {
            source = data.accountId;
            destination = data.solidCard?.name;
          } else {
            source = data.solidCard?.name;
            destination = data.accountId;
          }
          paidTo = data.card?.merchant?.name;
          break;
        case 'debitCard': // debit pull
          // it's only have debit
          source = data.debitCard?.name;
          destination = data.accountId;
          break;
        case 'sell':
        case 'buy':
          if (data.txnType === 'credit') {
            destination = data.accountId;
          } else {
            source = data.accountId;
          }
          break;
        default:
          description = data.description;
          break;
      }
    }
    const date = format(new Date(data.txnDate), dateFormat);

    return {
      source,
      destination,
      paidTo,
      bank,
      receivedFrom,
      description,
      transactionId,
      location,
      date
    };
  }, [data]);

  const reportIssue = useCallback(() => {
    getInTouch();
  }, []);

  return (
    <Box width="100%">
      <>
        {/* (Update loader) */}
        {false ? (
          <SkeletonLoader name="TransacitonDetails" />
        ) : null}
        {/* Summary */}
        <Box className={style.transaction__summary}>
          <HStack justifyContent="space-between" mb="16px">
            <Text data-testid="currency-data" variant="regular" fontSize={amountFontSize} color={data.txnType === 'credit' ? amountReceivedColor : amountSentColor} fontWeight={amountFontWeight} className={data.txnType === 'credit' ? style.transaction__received : style.transaction__sent}>
              {data.walletId ? (`${data.amount} ${data.currency}`) : currencyFormat(data.amount)}
            </Text>
            <Box className={`${style.tag} ${style[`tag__${data.transferType}`]}`}>
              <Text data-testid="transaction-type" variant="small" className={style.tag_text} textTransform="capitalize" textAlign="center">{t(`transaction_summary.${data.transferType}`, data.transferType)}</Text>
            </Box>
          </HStack>
          <Box>
            <Text data-testid="title" variant="regular" textAlign="left" className="text-dark">
              {data.title}
            </Text>
            <Text variant="small" data-testid="date" className="text-light" textAlign="left">{otherDetails.date}</Text>
          </Box>
          <Box />
        </Box>
        {/* Other Details */}
        <Box>
          <Text variant="large" className="transactions__title" mb="16px">{t('transaction_summary.otherDetails')}</Text>
          {data.walletId ? (
            <Box className={style.transaction__other_details}>
              <HStack py="8px" align="start">
                <Text variant="regular" w="40%" className="text-light" textAlign="left">{t('transaction_summary.source')}</Text>
                <Text variant="regular" w="60%" textAlign="left">{otherDetails.source}</Text>
              </HStack>
              <HStack py="8px" align="start">
                <Text variant="regular" w="40%" className="text-light" textAlign="left">{t('transaction_summary.destination')}</Text>
                <Text variant="regular" w="60%" textAlign="left">{otherDetails.destination}</Text>
              </HStack>
              <HStack py="8px" align="start">
                <Text variant="regular" w="40%" className="text-light" textAlign="left">{t('transaction_summary.quantity')}</Text>
                <Text variant="regular" w="60%" textAlign="left">{data.amount.replace('-', '')}</Text>
              </HStack>
              <HStack py="8px" align="start">
                <Text variant="regular" w="40%" className="text-light" textAlign="left">{t('transaction_summary.description')}</Text>
                <Text variant="regular" w="60%" textAlign="left">{otherDetails.description}</Text>
              </HStack>
              <HStack py="8px" align="start">
                <Text variant="regular" w="40%" className="text-light" textAlign="left">{t('transaction_summary.status')}</Text>
                <Text variant="regular" w="60%" textAlign="left">{data.status}</Text>
              </HStack>
            </Box>
          )
            : (
              <Box className={style.transaction__other_details}>
                {otherDetails.source
                  ? (
                    <HStack py="8px" align="start">
                      <Text variant="regular" w="40%" className="text-light" textAlign="left">{t('transaction_summary.source')}</Text>
                      <Text data-testid="source" variant="regular" w="60%" textAlign="left">{otherDetails.source}</Text>
                    </HStack>
                  ) : null }
                {otherDetails.destination
                  ? (
                    <HStack py="8px" align="start">
                      <Text variant="regular" w="40%" className="text-light" textAlign="left">{t('transaction_summary.destination')}</Text>
                      <Text data-testid="destination" variant="regular" w="60%" textAlign="left">{otherDetails.destination}</Text>
                    </HStack>
                  ) : null }
                {otherDetails.bank
                  ? (
                    <HStack py="8px" align="start">
                      <Text variant="regular" w="40%" className="text-light" textAlign="left">{t('transaction_summary.bank')}</Text>
                      <Text data-testid="bank" textTransform="capitalize" variant="regular" w="60%" textAlign="left">{otherDetails.bank}</Text>
                    </HStack>
                  ) : null }
                <HStack py="8px" align="start">
                  <Text variant="regular" w="40%" className="text-light" textAlign="left">{t('transaction_summary.transactionID')}</Text>
                  <Text variant="regular" w="60%" textAlign="left">{otherDetails.transactionId}</Text>
                </HStack>
                {otherDetails.description && otherDetails.description.length !== 0
                  ? (
                    <HStack py="8px" align="start">
                      <Text variant="regular" w="40%" className="text-light" textAlign="left">{t('transaction_summary.description')}</Text>
                      <Text data-testid="description" textTransform="capitalize" variant="regular" w="60%" textAlign="left">{otherDetails.description}</Text>
                    </HStack>
                  ) : null }
                <HStack py="8px" align="start">
                  <Text variant="regular" w="40%" className="text-light" textAlign="left">{t('transaction_summary.status')}</Text>
                  <Text data-testid="status" textTransform="capitalize" variant="regular" w="60%" textAlign="left">{data.status}</Text>
                </HStack>
              </Box>
            )}
        </Box>
        {/* Actions */}
        <Box>
          <Text variant="large" className="transactions__title" mb="16px">{t('transaction_summary.actions')}</Text>
          <HStack gap="8px" mt="16px" mb="32px">
            {showPdfButton
              ? <Button variant="primary" onClick={onExport} className={style.actions__action}>{t('transaction_summary.viewAsPdf')}</Button> : null }
            {showReportButton
              ? <Button variant="primary" onClick={reportIssue} className={style.actions__action}>{t('transaction_summary.reportIssue')}</Button> : null }
          </HStack>
        </Box>
        {/* Attachments */}
        {/* <Box>
      <Heading variant="primary" className="transactions__title" mr="auto" mb="16px">{t('transaction_summary.attachments')}</Heading>
      <Box className={style.attachments}>
        <Box className={`${style.attachment__container} ${attachmentsFullWidth ? style.attachment__container__full_width : null}`}>
          <Box className={style.attachment}>
            <Box className={style.attachment__img_container}>
              <Img className={style.attachment__img} alt="logo" src="https://via.placeholder.com/150" data-testid="logo" />
            </Box>
            <Text variant="branding"  pt="12px">{t('transaction_summary.invoice')}</Text>
            <Text variant="info">IN1001.pdf</Text>
          </Box>
        </Box>
        <Box className={`${style.attachment__container} ${attachmentsFullWidth ? style.attachment__container__full_width : null}`}>
          <Box className={style.attachment}>
            <Box className={style.attachment__img_container}>
              <Img className={style.attachment__img} alt="logo" src="https://via.placeholder.com/150" data-testid="logo" />
            </Box>
            <Text variant="branding"  pt="12px">{t('transaction_summary.receipt')}</Text>
            <Text variant="info">{t('transaction_summary.noAttachments')}</Text>
          </Box>
        </Box>
        <Box className={`${style.attachment__container} ${attachmentsFullWidth ? style.attachment__container__full_width : null}`}>
          <Box className={style.attachment}>
            <Box className={style.attachment__img_container}>
              <Img className={style.attachment__img} alt="logo" src="https://via.placeholder.com/150" data-testid="logo" />
            </Box>
            <Text variant="branding"  pt="12px">{t('transaction_summary.attachments')}</Text>
            <Text variant="info">IN1001.pdf</Text>
          </Box>
        </Box>
        <Box className={`${style.attachment__container} ${attachmentsFullWidth ? style.attachment__container__full_width : null}`}>
          <Box className={style.attachment}>
            <Box className={style.attachment__img_container}>
              <Img className={style.attachment__img} alt="logo" src="https://via.placeholder.com/150" data-testid="logo" />
            </Box>
            <Text variant="branding"  pt="12px">{t('transaction_summary.notes')}</Text>
            <Text variant="info">{t('transaction_summary.noAttachments')}</Text>
          </Box>
        </Box>
      </Box>
    </Box> */}
        {/* Location */}
        {data.transferType === 'card' && data.card?.merchant
          ? (
            <Box>
              <Text variant="large" className="transactions__title" mb="16px">{t('transaction_summary.location')}</Text>
              <Box className={style.location__container}>
                <Box className={style.location__map}>
                  <Map zoom={7} apiKey={process.env.REACT_APP_GMAP_KEY || ''} address={`${data.card?.merchant.merchantCity},${data.card?.merchant.merchantState},${data.card?.merchant.merchantCountry}`} />

                </Box>
                <Box className={style.location__info}>
                  <Text variant="regular" className="text-light" textAlign="left">{t('transaction_summary.address')}</Text>
                  <Text variant="branding">{ `${data.card?.merchant?.merchantName}, ${data.card?.merchant?.merchantCity}, ${data.card?.merchant?.merchantState}`}</Text>
                </Box>
                <Box className={style.location__info}>
                  <Text variant="regular" className="text-light" textAlign="left">{t('transaction_summary.country')}</Text>
                  <Text variant="branding">{ data.card?.merchant?.merchantCountry }</Text>
                </Box>
              </Box>
            </Box>
          )
          : null }
      </>
    </Box>

  );
};

export default TransactionDetails;
