import React, { useCallback, useEffect, useState } from 'react';
import { shallowEqual } from 'react-redux';
import { useTranslation } from 'react-i18next';
import { Box } from '@chakra-ui/react';
import TransactionDetailsComponent from './TransactionDetails';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import { getTransactionDetail } from '../../../store/middlewares/transaction';
import { transaction } from '../../../types/transaction';
import { apiErrorHandler } from '../../../utils/general';
import MiniBox from '../../../layout/MiniBox';
import style from './TransactionDetails.module.css';
import { useDrawer } from '../../../hooks/drawer';

const TransactionDetails: React.FC = () => {
  const { data } = useDrawer<{id: string}>();
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const { close } = useDrawer();

  const account = useAppSelector((store) => store.account.selectedAccount, shallowEqual);

  const [transactionDetails, updateTransaction] = useState<transaction| null>(null);

  const getDetails = useCallback((type: 'json' | 'pdf' = 'json') => {
    const accountId = account ? account.id : '';
    if (accountId !== '' && data?.id) {
      dispatch(getTransactionDetail(accountId, 'cash', data.id, type))
        .then((tr:any) => {
          if (type === 'pdf') {
            const url = window.URL.createObjectURL(tr);
            const a = document.createElement('a');
            a.href = url;
            a.download = `${data.id}-statement.pdf`;
            a.click();
            a.remove();
          } else {
            updateTransaction(tr);
          }
        }).catch(apiErrorHandler);
    }
  }, [data, account]);

  useEffect(() => {
    getDetails();
  }, [data, account]);
  return (
    <Box className="page">
      <MiniBox
        showBack
        bodyClass={style.bodyStyles}
        onBack={close}
        heading={t('transaction_summary.title')}
      >
        {transactionDetails
          ? <TransactionDetailsComponent data={transactionDetails} showReportButton showPdfButton onExport={() => getDetails('pdf')} amountReceivedColor="#2EC193" amountSentColor="#FF4A4A" /> : null }
      </MiniBox>
    </Box>

  );
};

export default TransactionDetails;
